import React, { useEffect, useState } from "react";
import { Text, Accordion, AccordionPanel, Box, Heading } from "grommet";
import { getCartForStore } from "../../library/parse";
import DataGrid from "../DataGrid";
import Config from "./OrderFormConfig";
import { PENDING_PAYMENT } from "../../constants/status";
const Home = ({ storeId }) => {
  const [carts, setCarts] = useState([]);
  const [total, setTotal] = useState(0);
  const [showPaymentInstruction, setShowPaymentInstruction] = useState(false);
  const [havePendingCart, setHavePendingCart] = useState(false);
  const [transactionNotes, setTranscationNotes] = useState("");

  useEffect(() => {
    getCartForStore(storeId).then(async (e) => {
      const newCarts = await Promise.all(
        e.map(async (o) => {
          await o.get("product").fetch();
          if (o.get("status") === PENDING_PAYMENT) {
            setHavePendingCart(true);
          }
          return o;
        })
      );
      setCarts(newCarts);
    });
  }, []);

  useEffect(() => {
    const newTotal = carts.reduce((p, n) => {
      return n.get("bid") + p;
    }, 0);
    setTotal(newTotal);
  }, [carts]);
  return (
    <Box>
      <DataGrid
        hideCRUDForm
        objectClass="Cart"
        columns={Config}
        query={(q) => q}
        datasource={carts}
      />
      <Box>
        <Heading level={4}>Pending Payment: ${`${total}`}</Heading>
      </Box>
    </Box>
  );
};

export default Home;
