import React, { useEffect, useState } from "react";
import { Text, Accordion, AccordionPanel, Box, Button } from "grommet";
import { getMyCart } from "../library/parse";
import Order from "../containers/Order";

const Home = () => {
  const [carts, setCarts] = useState([]);
  useEffect(() => {
    getMyCart().then(async (e) => {
      const cartAndProducts = await Promise.all(
        e.map(async (cart) => {
          return await cart
            .get("product")
            .fetch()
            .then((product) => ({
              cart,
              product,
            }));
        })
      );
      setCarts(cartAndProducts);
    });
  }, []);
  return (
    <>
      <Order />
    </>
  );
};

export default Home;
